<template>
  <v-card>
    <v-card-title>
      <v-row no-gutters align="center">
        <span><v-icon>edit</v-icon></span>
        <p contenteditable ref="namePlantilla" class="px-2 my-0">Plantilla sin nombre</p>
      </v-row>
    </v-card-title>
    <v-card-text>
      <h3>Cabecera</h3>
      <draggable
        v-model="cabecera"
        @start="drag=true"
        @end="drag=false"
        style="width: 100%; min-height:50px"
        @change="change"
        group="tr">
        <div v-for="(term, index) in cabecera" :key="index">
          <v-chip
            class="ma-2"
            color="primary"
            text-color="white"
            close
            @click:close="removeItem(term)"
            @dblclick="setDItem(term)"
            >
            {{term.title}}
          </v-chip>
        </div>
      </draggable>
      <v-divider></v-divider>
      <h3>Cuerpo</h3>
      <v-container
        id="scroll-target"
        style="max-height: 300px"
        class="overflow-y-auto"
        >
        <draggable
          v-model="termrefs"
          @start="drag=true"
          @end="drag=false"
          style="width: 100%; min-height:100px"
          @change="change"
          group="tr">
          <div v-for="(term, index) in termrefs" :key="index">
            <v-chip
              class="ma-2"
              color="primary"
              text-color="white"
              close
              @click:close="removeItem(term)"
              @dblclick="setDItem(term)"
              >
              {{term.title}}
            </v-chip>
          </div>
        </draggable>
      </v-container>
      <v-divider></v-divider>
      <h3>Pie de página</h3>
      <draggable
        v-model="pie"
        @start="drag=true"
        @end="drag=false"
        style="width: 100%; min-height:50px"
        @change="change"
        group="tr">
        <div v-for="(term, index) in pie" :key="index">
          <v-chip
            class="ma-2"
            color="primary"
            text-color="white"
            close
            @click:close="removeItem(term)"
            @dblclick="setDItem(term)"
            >
            {{term.title}}
          </v-chip>
        </div>
      </draggable>
      <v-row justify="end" no-gutters class="mt-3">
        <v-btn :loading="loading" color="primary" small @click="save" :disabled="tipoContratacion === ''">{{txtSave}}</v-btn>
      </v-row>
      <v-dialog v-model="dialog" width="650">
        <v-card>
          <v-card-title class="text-h5 grey lighten-2">
            {{dItem.title}}
          </v-card-title>
          <v-card-text>
            {{dItem.tipo}}
            {{dItem.text}}
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-card-text>
  </v-card>
</template>
<script>
import smallAdj from '@/components/solProcContrata/smallAdj'
import draggable from 'vuedraggable'
import { plantillas } from '@/servicios/PlantillasService.js'
import AddFilesToStorage from '@/components/shared/AddFilesToStorage'
export default {
  props: ['idPlantilla', 'tipoContratacion', 'editItemPlantilla'],
  components: { draggable, smallAdj, AddFilesToStorage },
  data () {
    return {
      loading: false,
      termrefs: [],
      docHead: {},
      docFooter: {},
      fileReset: 0,
      fileResetDos: 145,
      dItem: '',
      dialog: false,
      cabecera: [],
      pie: [],
      selectedItem: ''
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    async getData() {
      if (this.idPlantilla) {
        const {data} = await plantillas.getDataPlantilla({ id: this.idPlantilla })
        this.cabecera = data.detalle.filter(item => item.tipo === 'header')
        this.termrefs = data.detalle.filter(item => item.tipo === 'body')
        this.pie = data.detalle.filter(item => item.tipo === 'footer')
        this.$refs.namePlantilla.innerText = data.titulo
      }
    },
    async save () {
      const title = this.$refs.namePlantilla.innerHTML
      const tipoContratacion = this.tipoContratacion
      if (this.termrefs.length > 0 ) {
        let cloneTermref = [].concat(this.cabecera, this.termrefs, this.pie)
        console.log(cloneTermref)
        try {
          if (this.txtSave === 'Guardar') var {data} = await plantillas.savePlantilla({ title: title, tipoContratacion, data: cloneTermref })
          else if (this.txtSave === 'Editar') var {data} = await plantillas.editPlantilla({ id: this.idPlantilla, title: title, tipoContratacion, data: cloneTermref })
          if (data.tipo === 'success') {
            /* if (this.txtSave === 'Guardar') {
              this.termrefs = []
              this.docHead = { file_name: '', file_path: '' }
              this.docFooter = { file_name: '', file_path: '' }
              this.$refs.namePlantilla.innerText = 'Plantilla sin nombre'
              this.$emit('resetear')
            } else if (this.txtSave === 'Editar') {
              this.getData()
            } */
            this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
            this.$router.push('/PlantillasTermRef')
          } else if (data.tipo === 'error') {
            this.$toasted.error(data.respuesta, { icon: 'error', duration: 3000 })
          }
        } catch (error) {
          console.log(error)
        } finally {
          this.loading = false
        }
      } else {
        this.$toasted.error('No hay items por guardar.', { icon: 'error', duration: 3000 })
      }
    },
    change (ev) {
      console.log(ev)
      if (ev.added) {
        const element = ev.added.element
        this.cabecera.map(item => {
          if (JSON.stringify(item) === JSON.stringify(element)) {
            item.tipo = 'header'
          }
        })
        this.termrefs.map(item => {
          if (JSON.stringify(item) === JSON.stringify(element)) {
            item.tipo = 'body'
          }
        })
        this.pie.map(item => {
          if (JSON.stringify(item) === JSON.stringify(element)) {
            item.tipo = 'footer'
          }
        })
      }
    },
    removeItem (term) {
      const tipo = term.tipo
      if (tipo === 'header') {
        this.cabecera.splice(this.cabecera.indexOf(term), 1)
      } else if (tipo === 'body') {
        this.termrefs.splice(this.termrefs.indexOf(term), 1)
      } else {
        this.pie.splice(this.pie.indexOf(term), 1)
      }
      this.$emit('removed', term)
    },
    actAdjHead (adj) {
      this.docHead = adj
    },
    actAdjFooter (adj) {
      this.docFooter = adj
    },
    setDItem (term) {
      this.selectedItem = term
      const element = { ...term }
      element.from = 'plantillas'
      this.$emit('edit-item', element)
      /* this.dItem = term
      this.dialog = !this.dialog */
    }
  },
  computed: {
    txtSave () {
      return this.idPlantilla ? 'Editar' : 'Guardar'
    }
  },
  watch: {
    dialog (val) {
      if (!val) {
        this.dItem = ''
      }
    },
    editItemPlantilla (val) {
      this.termrefs.map(item => {
        if (item === this.selectedItem) {
          item.title = val.title
          item.text = val.text
          item.file_name = val.file_name
          item.file_path = val.file_path
          item.file_size = val.file_size
          item.file_type = val.file_type
        }
      })
    }
  }
}
</script>
