<template>
  <v-container>
    <h2>{{txtTitle}}</h2>
    <div class="mt-5">
      <v-select
        :items="tiposContratacion"
        label="Tipo contración"
        v-model="tipoContratacion"
        dense
        :disabled="idPlantilla ? true : false"
        :loading="loading"
        >
      </v-select>
    </div>
    <v-row>
      <v-col :cols="showNew ? 8 : 6">
        <TermrefItems
          v-show="!showNew && tipoContratacion"
          :newTermrefItem="newTermrefItem"
          :editItemList="editItemList"
          :tipoContratacion="tipoContratacion"
          :key="reset"
          @add-element="showNew=!showNew"
          @reset-termref="newTermrefItem=''"
          @edit-item="setEditItem"
          >
        </TermrefItems>
        <NewTermrefItem
          v-if="showNew"
          :editItem="editItem"
          @cancel="showNew=!showNew"
          @save="saveNewTermrefItem">
          >
        </NewTermrefItem>
      </v-col>
      <v-col :cols="showNew ? 4 : 6">
        <TermrefPlantilla
          v-if="tipoContratacion"
          :idPlantilla="idPlantilla"
          :tipoContratacion="tipoContratacion"
          :editItemPlantilla="editItemPlantilla"
          @removed="removed"
          @resetear="reset++"
          @edit-item="setEditItem"
          >
        </TermrefPlantilla>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import TermrefItems from '@/components/plantillas/Termref_items';
import TermrefPlantilla from '@/components/plantillas/Termref_plantilla';
import TiposContratacionService from '@/servicios/TiposContratacionService.js'
import { plantillas } from '@/servicios/PlantillasService.js'
export default {
  components: { TermrefItems, TermrefPlantilla,
    NewTermrefItem: () => import('@/components/plantillas/New_termref_item')
  },
  data () {
    return {
      reset: 1,
      showNew: false,
      newTermrefItem: '',
      tiposContratacion: [],
      tipoContratacion: '',
      loading: false,
      editItem: '',
      editItemPlantilla: '',
      editItemList: '',
    }
  },
  async mounted () {
    this.loading = true
    Promise.all([this.getTiposContracion(), this.getData()])
      .then(resolve => {
        console.log(resolve)
      })
      .catch(error => {
        console.error(error)
      })
      .finally(() => {
        this.loading = false
      })
  },
  methods: {
    async getTiposContracion() {
      try {
       const result = await TiposContratacionService.getTiposForContratacion()
       this.tiposContratacion = result.data.tiposContratacion
      } catch (error) {
        console.error(error)
      }
    },
    async getData() {
      if (this.idPlantilla) {
        try {
          const {data} = await plantillas.getDataPlantilla({ id: this.idPlantilla })
          this.tipoContratacion = data.tipoContratacion
        } catch (error) {
          console.error(error)
        }
      }
    },
    saveNewTermrefItem (item) {
      // hay que decidir si es una edicion o una creacion
      console.log(item)
      if (item.from === 'plantillas') {
        this.editItemPlantilla = item
      } else if (item.from === 'lista'){
        this.editItemList = item
      } else {
        this.newTermrefItem = item
      }
      this.showNew = !this.showNew
    },
    removed (item) {
      // lista recupera el item eliminado
      this.newTermrefItem = item
    },
    setEditItem (item) {
      console.log(item)
      this.editItem = item
      this.showNew = true
    }
  },
  computed: {
    txtTitle () {
      const id = this.$route.params.id
      return id ? `Plantilla N° ${id}` : 'Nueva plantilla'
    },
    idPlantilla () {
      return this.$route.params.id
    }
  },
  watch: {
    showNew (val) {
      if (!val) {
        this.editItem = ''
      }
    }
  }
}
</script>
