<template>
  <div>
    <v-card>
      <v-card-title color="primary" dark>
        <div>Elementos disponibles</div>
        <v-spacer></v-spacer>
        <v-btn @click="$emit('add-element')" color="primary" small fab><v-icon>add</v-icon></v-btn>
      </v-card-title>
      <v-card-text>
        <draggable v-model="termrefs" @start="drag=true" @end="drag=false" style="width: 100%" group="tr">
          <div v-for="(term, index) in termrefs" :key="index">
            <v-chip
              class="ma-2"
              color="primary"
              text-color="white"
              @dblclick="setDItem(term)"
              >
              {{term.title}}
            </v-chip>
          </div>
        </draggable> 
      </v-card-text>
    </v-card>
  </div>
</template>
<script>
import draggable from 'vuedraggable'
import { plantillas } from '@/servicios/PlantillasService.js'
export default {
  components: { draggable },
  props: ['tipoContratacion', 'newTermrefItem', 'editItemList'],
  data () {
    return {
      titulo: '',
      cuerpo: '',
      dialog: false,
      termrefs: [],
      selectedItem: '',
      offsetTop: 0
    }
  },
  async mounted () {
    
  },
  methods: {
    async getItemsPlantilla (tipoContratacion) {
      try {
        const { data } = await plantillas.getItemsDisp({ tipoContratacion })
        console.log(data)
        const lista = data
        // si viene en un edit
        const idTermref = this.$route.params.id
        if (idTermref) {
          const { data } = await plantillas.getDataPlantilla({ id: idTermref })
          const plantillaItems = data.detalle.filter(item => item.tipo === 'body')
          console.log(plantillaItems)
          var finals = lista.filter(e => !plantillaItems.find(a => e.title === a.title))
          this.termrefs = finals
        } else {
          this.termrefs = lista
        }
      } catch (error) {
        console.error(error)
      }
    },
    setDItem (term) {
      this.selectedItem = term
      const element = { ...term }
      element.from = 'lista'
      this.$emit('edit-item', element)
    },
    onScroll (e) {
      this.offsetTop = e.target.scrollTop
    }
  },
  watch: {
    newTermrefItem (val) {
      console.log(val)
      if (val) {
        this.termrefs.push(val)
        this.$emit('reset-termref')
      }
    },
    tipoContratacion (newValue) {
      if (newValue) {
        this.getItemsPlantilla(newValue)
      }
    },
    editItemList (val) {
      console.log(val)
      if (val) {
        this.termrefs.map(item => {
          if (item === this.selectedItem) {
            console.log('founded')
            item.title = val.title
            item.text = val.text
            item.file_name = val.file_name || ''
            item.file_path = val.file_path || ''
            item.file_size = val.file_size || ''
            item.file_type = val.file_type || ''
          }
        })
      }
    }
  }
}
</script>
